const app = getApp()
export default {
	data() {
		return {
			playing: false,
			videoContext: {},

			fullScreen: false,
			playUrl: "http://5815.liveplay.myqcloud.com/live/5815_89aad37e06ff11e892905cb9018cf0d4_550.flv",
			orientation: "vertical",
			objectFit: "contain",
			muted: false,
			backgroundMuted: false,
			debug: false,

			headerHeight: app.globalData.headerHeight,
			statusBarHeight: app.globalData.statusBarHeight,
		}
	},
	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {


	},

	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady: function() {
		this.createContext();
		console.log(this.data.videoContext);
		uni.setKeepScreenOn({
			keepScreenOn: true,
		})
	},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {
		// 保持屏幕常亮
		uni.setKeepScreenOn({
			keepScreenOn: true,
		})
	},

	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {

	},

	/**
	 * 生命周期函数--监听页面卸载
	 */
	onUnload: function() {
		this.stop();
		uni.setKeepScreenOn({
			keepScreenOn: true,
		})
	},

	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onPullDownRefresh: function() {

	},

	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {

	},

	/**
	 * 用户点击右上角分享
	 */
	onShareAppMessage: function() {
		return {
			// title: '直播播放器',
			// path: '/pages/play/play',
			path: '/pages/home-page/main',
			imageUrl: 'https://mc.qcloudimg.com/static/img/dacf9205fe088ec2fef6f0b781c92510/share.png'
		}
	},

	/**
	 * 用户点击右上角分享
	 */
	onShareAppMessage: function() {
		console.log("onLoad onShareAppMessage");
		return {
			// title: 'RTMP推流',
			// path: '/pages/push/push',
			path: '/pages/home-page/main',
			imageUrl: 'https://mc.qcloudimg.com/static/img/dacf9205fe088ec2fef6f0b781c92510/share.png'
		}
	},

	methods: {

		onScanQR: function() {
			this.stop();
			this.createContext();
			var self = this;
			uni.scanCode({
				onlyFromCamera: true
				// success: (res) => {
				// 	console.log(res);
				// 	self.setData({
				// 		playUrl: res.result
				// 	})
				// }
			})
		},

		onPlayClick: function() {
			var url = this.playUrl;
			if (url.indexOf("rtmp:") == 0) {} else if (url.indexOf("https:") == 0 || url.indexOf("http:") == 0) {
				if (url.indexOf(".flv") != -1) {}
			} else {
				uni.showToast({
					title: '播放地址不合法，目前仅支持rtmp,flv方式!',
					icon: 'loading',
				})
			}
				this.playing=!this.playing
			if (this.playing) {
				this.data.videoContext.play();
				console.log("video play()");
				wx.showLoading({
					title: '',
				})
			} else {
				this.data.videoContext.stop();
				console.log("video stop()");
				wx.hideLoading();
			}
		},

		onOrientationClick: function() {
			if (this.orientation == "vertical") {
				this.orientation = "horizontal";
			} else {
				this.orientation = "vertical";
			}

				this.orientation= this.data.orientation
		},

		onObjectfitClick: function() {
			if (this.objectFit == "fillCrop") {
				this.objectFit = "contain";
			} else {
				this.objectFit = "fillCrop";
			}
				this.objectFit= this.objectFit
			
		},

		onLogClick: function() {
			this.debug= !this.debug
			var self = this;
			setTimeout(function() {
				this.exterFlag= !self.exterFlag
			}, 10);
		},

		onMuteClick: function() {
				this.muted=!this.data.muted
		},

		onFullScreenClick: function() {

			if (!this.fullScreen) {
				this.videoContext.requestFullScreen({
					direction: 0,

				})

			} else {
				this.videoContext.exitFullScreen({

				})
			}
		},

		onPlayEvent: function(e) {
			console.log(e.detail.code);
			if (e.detail.code == -2301) {
				this.stop();
				uni.showToast({
					title: '拉流多次失败',
				})
			}
			if (e.detail.code == 2004) {
				uni.hideLoading();
			}
		},

		onFullScreenChange: function(e) {
			this.fullScreen= e.detail.fullScreen
			console.log(e);
			uni.showToast({
				title: this.data.fullScreen ? '全屏' : '退出全屏',
			})
		},

		stop: function() {
			this.playing=false,
			//this.playUrl= "rtmp://2157.liveplay.myqcloud.com/live/2157_wx_live_test1",
			this.orientation= "vertical",
			this.objectFit= "contain",
			this.muted= false,
			this.fullScreen= false,
			this.backgroundMuted= false,
			this.debug= false,
			this.exterFlag= false,
			this.videoContext.stop();
			uni.hideLoading();
		},

		createContext: function() {
			this.videoContext= uni.createLivePlayerContext("video-livePlayer")
		},
		onBack: function () {
			uni.navigateBack({
			delta: 1
			});
		}

	}
}
